/**
 * MFA Request Code
 * @author Julian D. Mora Ramos
 */

bizagi.login_multiple.widgets.requestcode.extend("bizagi.login_multiple.widgets.requestcode", {}, {
  init: function init(loginFacade, dataService) {
    this._super(loginFacade, dataService);
  },
  postRender: function postRender(params) {
    var self = this;
    var template = self.loginFacade.getTemplate("requestcode");
    self.content = self.getContent();
    self.params = params || {};

    // Render Form
    var form = $.tmpl(template, {});

    $("#form-content", self.content).append(form);

    // Create object of message controller
    self.messageController = new bizagi.login_multiple.message(self.loginFacade, $("#login-message-wrapper", self.content));

    // Set events
    self.eventHandler();
    self.initialize();
  },
  eventHandler: function eventHandler() {
    var self = this;
    var content = self.getContent();

    var codeInput = $("#code", content);
    var btnVerify = $("#btn-validate-code", content);
    var btnSendCode = $("#btn-send-code", content);
    var btnResendSMS = $("#mfaResendSMS", content);
    var btnResendMail = $("#mfaResendMail", content);

    //#region Declare functions

    function isValidCode(code) {
      return /^[0-9]+$/.test(code);
    }

    function toggleClassValidate(evt) {
      var state = isValidCode(evt && evt.target ? evt.target.value : '');
      btnVerify.toggleClass("btn-verify-disabled", !state);
      btnVerify.toggleClass("valid", state);
    }

    /**
       * @return {void}
       */
    function verifyCode() {
      var code = codeInput.val();

      var params = $.extend({}, { verificationCode: code }, self.dataService.paramsUser);

      self.cleanErrors();

      $.when(self.dataService.verifyCode(params)).done(function (response) {
        if (bizagi.util.parseBoolean(response.isAuthenticate)) {
          self.loginFacade.setBizagiAuthentication(response);
        } else {
          if (response.status === "123" || response.messageStatus === "123") {
            self.messageController.showWidgetCustomMessage(response.message);
            setTimeout(function () {
              self.loginFacade.changeWidget("BIZAGI_LOGIN_WIDGET_LOGIN");
            }, 2500);
          } else {
            self.messageController.showWidgetCustomMessage(response.hasError ? response.error || response.message : self.resource("workportal-login-multifactor-invalid-code"));
          }
        }
      }).fail(function () {
        self.messageController.showWidgetCustomMessage(self.resource("workportal-general-error-generic"));
      });
    }

    function getValueChecked() {
      return $("input[name='choiceSend']:checked").val() || "sms";
    }

    function autoChangeInformativeMessage() {
      var res = self.params.response || {};
      if (res.MFAParameters) {
        var mfa_params = res.MFAParameters;
        var val = getValueChecked().toLowerCase();

        self.valuesMFA = {
          email: mfa_params['emailKey'],
          phone: mfa_params['phoneKey'] };


        setTextDestination(val, self.valuesMFA.phone, self.valuesMFA.email);
      }
    }

    /**
       * @param {'sms'|'email'} type
       * @param {string} valuePhone
       * @param {string} valueEmail
       */
    function setTextDestination(type, valuePhone, valueEmail) {
      if (type === "sms") {
        $("#messageDestination", content).text(self.resource("workportal-login-multifactor-message-sms-sent", [valuePhone]));
      } else {
        $("#messageDestination", content).text(self.resource("workportal-login-multifactor-message-email-sent", [valueEmail]));
      }
    }

    //#endregion

    //#region Declare events
    $("#btn-validate-code", content).bind("click", function (ev) {
      ev.preventDefault();
      verifyCode();
    });

    btnSendCode.bind("click", function (ev) {
      ev.preventDefault();

      var toSend = getValueChecked();

      self.cleanErrors();

      self.resendVerificationCode(toSend, function (response, error) {
        if (response && response.success) {
          self.hide("#templateSelectDestination");
          self.show("#templateRequestCode");
        } else if (response && response.hasError && typeof response.message === "string" && response.message.length > 0) {
          self.messageController.showWidgetCustomMessage(response.error || response.message);
        } else {
          self.messageController.showWidgetCustomMessage(self.resource("workportal-general-error-generic"));
        }
      });
    });

    btnResendSMS.bind("click", function (ev) {
      ev.preventDefault();
      self.resendVerificationCode("SMS");
      setTextDestination("sms", self.valuesMFA.phone, self.valuesMFA.email);
    });

    btnResendMail.bind("click", function (ev) {
      ev.preventDefault();
      self.resendVerificationCode("email");
      setTextDestination("email", self.valuesMFA.phone, self.valuesMFA.email);
    });

    codeInput.bind("change paste keyup", function (evt) {
      self.cleanErrors();
      toggleClassValidate(evt);
    });

    $("input[name='choiceSend']", content).change(function () {
      self.cleanErrors();
      autoChangeInformativeMessage();
    });

    $("#action_goto_step1", content).bind("click", function () {
      self.cleanErrors();
      self.loginFacade.changeWidget("BIZAGI_LOGIN_WIDGET_LOGIN", {
        goToStep2: true,
        selectedDomain: self.params.selectedDomain });

    });
    //#endregion

    autoChangeInformativeMessage();
  },

  cleanErrors: function cleanErrors() {
    this.messageController.cleanErrorsQueue();
    this.messageController.cleanSuccessQueue();
    this.messageController.hideErrors();
  },

  /**
      * Enviar codigo de verificacion
      * @param {'SMS' | 'email'} providerType
      * @param {Function} [cb]
      * @return {void}
      */
  resendVerificationCode: function resendVerificationCode(providerType, cb) {
    var self = this;
    var params = { providerType: providerType };

    if (typeof cb !== "function") {
      cb = new Function();
    }

    self.cleanErrors();

    $.when(self.dataService.resendVerifyCode(params)).done(function (response) {
      cb(response, null);
    }).fail(function (err) {
      self.messageController.showWidgetCustomMessage(self.resource("workportal-general-error-generic"));
      cb(null, err);
    });
  },

  initialize: function initialize() {
    var self = this;
    var content = self.getContent();

    // Cambiar valores de correo y telefono.
    var res = self.params.response || {};
    if (res.MFAParameters) {
      var mfa_params = res.MFAParameters;

      var emailValue = mfa_params['emailKey'];
      var phoneValue = mfa_params['phoneKey'];
      var deletedCount = 0;

      if (emailValue && emailValue !== "") {
        $("label[for='inputEmail']", content).text(self.resource("workportal-login-multifactor-option-email", [emailValue]));
      } else {
        $("#itemEmail", content).remove();
        $("#mfaResendMail", content).remove();
        $(".login-verify-code-hr", content).remove();
        $("#inputSms", content).prop("checked", true);
        deletedCount++;
      }

      if (phoneValue && phoneValue !== "") {
        $("label[for='inputSms']", content).text(self.resource("workportal-login-multifactor-option-sms", [phoneValue]));
      } else {
        $("#itemSMS", content).remove();
        $("#mfaResendSMS", content).remove();
        $(".login-verify-code-hr", content).remove();
        $("#inputEmail", content).prop("checked", true);
        deletedCount++;
      }

      if (deletedCount >= 2) {
        $("#templateSelectDestination > p", content).html("<p>" + self.resource("workportal-login-multifactor-empty-destination") + "</p>");
        $("#btn-send-code", content).remove();
        $("#templateRequestCode", content).remove();
      }
    } else {
      console.warn("No existen parametros MFA");
    }
  },

  hide: function hide(selector) {
    $(selector, this.getContent()).addClass("hide");
    $(selector).css({ "display": "none" });
  },

  show: function show(selector) {
    $(selector, this.getContent()).removeClass("hide");
    $(selector).css({ "display": "" });
  },

  /**
      *
      * @param {string} message
      */
  changeLabelMessage: function changeLabelMessage(message) {
    var content = this.getContent();
    $("#spanSegment", content).text(message || '');
  },

  /**
      *
      * @param {string} key
      * @param {string[]|number[]} [params]
      * @return {string}
      */
  resource: function resource(key, params) {
    var val = bizagi.localization.getResource(key);
    return params ? val.replace(/{(\d+)}/g, function (match, number) {
      return typeof params[number] != 'undefined' ? params[number] : match;
    }) : val;
  } });