bizagi.login_multiple.factory.extend("bizagi.login_multiple.factory", {}, {
  /*
                                                                             *   Load all the templates used in the workportal
                                                                             */
  loadTemplates: function loadTemplates() {
    var self = this;
    var defer = new $.Deferred();

    var template = bizagi.getTemplate("bizagi.loginexternal_multiple.desktop.login");

    $.when(
    self.loadTemplate("login-wrapper", template + "#ui-bizagi-login-wrapper"),
    self.loadTemplate("normal-login", template + "#ui-bizagi-login-normal-login"),
    self.loadTemplate("login-error-message", template + "#ui-bizagi-login-error-message"),
    self.loadTemplate("quick-login", template + "#ui-bizagi-login-quick-login"),
    self.loadTemplate("requestcode", template + "#ui-bizagi-login-requestcode")).
    done(function () {
      // Resolve when all templates are loaded
      defer.resolve();
    });
    return defer.promise();
  } });