bizagi.login_multiple.widgets.quicklogin.extend("bizagi.login_multiple.widgets.quicklogin", {}, {
  init: function init(loginFacade, dataService) {
    this._super(loginFacade, dataService);
  },
  postRender: function postRender(params) {
    var self = this;
    var template = self.loginFacade.getTemplate("quick-login");
    var content = self.getContent();
    self.params = params;

    $.when(self.dataService.getQuickLoginUserList(self.params.selectedDomain)).done(function (users) {
      if (users.error && users.error_redirect_uri) {
        window.location.href = users.error_redirect_uri;
      } else
      {
        var item = { item: users.users.length };

        // Render Form
        var form = $.tmpl(template, users, item);

        $("#form-content", self.content).append(form);

        // Create object of message controller
        self.messageController = new bizagi.login_multiple.message(self.loginFacade, $("#login-message-wrapper", content));

        // Set events
        self.eventHandler();
      }
    });
  },
  eventHandler: function eventHandler() {
    var self = this;
    var content = self.getContent();
    var userInput = $("#username", content);
    var loginOption = "alwaysAsk";
    var oAuthParams = self.loginFacade.oAuthParameters || {};

    $("#btn-login", content).bind("click", function (event) {
      event.preventDefault();
      self.quickLogin({
        domain: self.params.selectedDomain,
        user: userInput.val(),
        password: "",
        loginOption: loginOption,
        type: oAuthParams.type || "",
        oAuth2InternalState: oAuthParams.oAuth2InternalState || "",
        keepAuthentication: oAuthParams.oauth2sso || "true" });

    });

    $("#action_goto_step1", content).click(function () {
      self.loginFacade.changeWidget("BIZAGI_LOGIN_WIDGET_LOGIN", {});
    });
  },
  quickLogin: function quickLogin(params) {
    var self = this;

    if (params.user == "") {
      self.messageController.addErrorMessage(bizagi.localization.getResource("login-user-name-empty"));
    }

    if (!self.messageController.hasErrors()) {
      $.when(self.dataService.userLogin(params)).done(function (response) {
        if (bizagi.util.parseBoolean(response.isAuthenticate)) {
          self.loginFacade.setBizagiAuthentication(response);
        } else {
          self.messageController.showWidgetCustomMessage(response.status, "error");
        }
      }).fail(function (message) {
        self.messageController.showWidgetCustomMessage("generic", "error");
      });

    } else {
      self.messageController.showErrors();
    }
  } });