/**
 * Services for login
 */

$.Class.extend("bizagi.login_multiple.services.service", {}, {
  /**
                                                               * Constructor
                                                               */
  init: function init(params) {
    this.serviceLocator = new bizagi.render.services.context(params);
    this.defaultParams = params;
  },
  /**
     * Get bizagi configuration
     * @param {Function}   type    callback function when the file load is succed
     * @return {deferred} ajax object with JSON content
     */
  getConfiguration: function getConfiguration() {
    // Check session storage
    var bizagiConfig = window.sessionStorage.getItem("BizagiConfiguration");
    if (bizagiConfig) {
      var def = new $.Deferred();
      def.resolve(JSON.parse(bizagiConfig));
      return def.promise();
    } else {
      var self = this;
      var url = self.serviceLocator.getUrl("authenticationConfig");

      return $.read(url);
    }
  },
  /**
     * Login user 
     * @loginParams {object} loginParams Object with login credentials
     * @return {deferred} ajax object with JSON content
     */
  userLogin: function userLogin(loginParams) {
    var self = this;
    var url = self.serviceLocator.getUrl("oauth2IdPServerLoginPage");
    var type = loginParams.type || "";

    if (type && (type == 'oauth2AsServer' || type == 'oauth2AsBridge')) {
      var oAuth2InternalState = loginParams.oAuth2InternalState || "";
      if (oAuth2InternalState) {
        loginParams.oAuth2InternalState = oAuth2InternalState;
        url = self.serviceLocator.getUrl("oauth2IdPServerLoginPage");
      }
    }

    //Call to the restfull service for authentication
    return $.create(url, loginParams);
  },
  /**
      * Get list of bizagi users
      * @param {string} domain
      *
      * @return {deferred} ajax object with JSON content
      */
  getQuickLoginUserList: function getQuickLoginUserList(domain) {
    var self = this;
    var url = self.serviceLocator.getUrl("quickLoginUsers") + "?domain=" + domain;

    return $.read(url);
  },

  /*
     *   Return the state of the login cookie
     */
  readUserCookies: function readUserCookies() {
    var self = this;
    var url = self.serviceLocator.getUrl("readUserCookies");
    return $.read(url);
  },
  getLogo: function getLogo() {
    var self = this;
    return $.read({
      url: self.serviceLocator.getUrl("logo") }).
    pipe(function (data) {
      var result = {};
      if (typeof data.logo != "undefined") {
        try {
          var logoObj = JSON.parse(data.logo);
          result.logo = logoObj.login;
        } catch (e) {
          //  Nothing todo
        }
      }
      return result;
    });
  },

  /**
      *
      * @param {{ code: number }} params
      */
  verifyCode: function verifyCode(params) {
    var self = this;
    var url = self.serviceLocator.getUrl("verifyCode");

    return $.create(url, params);
  },

  /**
      *
      * @param {{providerType: 'SMS' | 'email'}} params
      */
  resendVerifyCode: function resendVerifyCode(params) {
    var self = this;
    var url = self.serviceLocator.getUrl("resendVerifyCode");
    return $.create(url, params);
  } });