function isQueueBusy(options) {
  var queue = bizagi.services.ajax.queues[options.queue];
  if (queue === undefined) {
    bizagi.services.ajax.queues[options.queue] = false;
    return false;
  }
  return bizagi.services.ajax.queues[options.queue];
}

function useQueue(options) {
  //console.log("[" + options.rnd + "] using queue " + options.queue);
  bizagi.services.ajax.queues[options.queue] = true;
}

function freeQueue(options) {
  //console.log("[" + options.rnd + "] freeing queue " + options.queue);
  bizagi.services.ajax.queues[options.queue] = false;
}

function waitForQueue(options) {
  return bizagi.util.sleepUntil(function () {
    var queueFree = !isQueueBusy(options);
    return queueFree;
  });
}

function validateQueueOptions(options) {
  if (!bizagi.util.isEmpty(options.queue)) {
    //console.log("[" + options.rnd + "] waiting for queue " + options.queue);
    return waitForQueue(options).then(function () {
      //console.log("[" + options.rnd + "] queue ready " + options.queue);
      useQueue(options);
      return true;
    });
  }
  return true;
}
