function getAjaxSizeJsonObjectInMegaBytes(jsonObject) {
  var size = new TextEncoder().encode(JSON.stringify(jsonObject)).length;
  return size / 1024 / 1024;
}

function saveMobileRequestResponse() {
  // object request / response
  var DIAGNOSTICS_REQUIRE_PROPERTY = 'configHeaders';
  var DIAGNOSTICS_GREAT_STATUS_CODE = 200;
  var DIAGNOSTICS_MAX_SIZE_REQUEST_MB = 1;
  var DIAGNOSTICS_MAX_SIZE_RESPONSE_MB = 2;
  var DIAGNOSTICS_MAX_SIZE_QUERY_STRING_MB = 0.3;
  var DIAGNOSTICS_ENDPOINT = "api/errorlogs/import";
  var DIAGNOSTICS_MAX_HTTP_REQUEST_RESPONSE_IN_QUEUE = 5;

  if (bizagi.util.isMobileErrorTracesEnabled()) {
    // Only for valid Request and response
    var requestResponseValid = typeof bizagi.services.ajax.mobileGeneral.requestURL !== "undefined" &&
    bizagi.services.ajax.mobileGeneral.requestURL.toString().toLocaleLowerCase().includes(DIAGNOSTICS_ENDPOINT) ? false : true;

    // Only request with data
    if (requestResponseValid && bizagi.services.ajax.mobileRequest.hasOwnProperty(DIAGNOSTICS_REQUIRE_PROPERTY) && bizagi.isMobile()) {

      var mobileRequestSize = getAjaxSizeJsonObjectInMegaBytes(bizagi.services.ajax.mobileRequest);
      var mobileResponseSize = getAjaxSizeJsonObjectInMegaBytes(bizagi.services.ajax.mobileResponse);
      var mobileRequestParametersSize = getAjaxSizeJsonObjectInMegaBytes(bizagi.services.ajax.mobileQueryStringParameters);

      // new request and response object
      var ajaxMobile = {
        general: bizagi.services.ajax.mobileGeneral,
        request: mobileRequestSize < DIAGNOSTICS_MAX_SIZE_REQUEST_MB ? bizagi.services.ajax.mobileRequest : undefined,
        response: mobileResponseSize < DIAGNOSTICS_MAX_SIZE_RESPONSE_MB ? bizagi.services.ajax.mobileResponse : undefined,
        queryStringParameters: mobileRequestParametersSize < DIAGNOSTICS_MAX_SIZE_QUERY_STRING_MB ? bizagi.services.ajax.mobileQueryStringParameters : undefined };


      // for queue business rules
      if (bizagi.services.ajax.mobileHttpQueue.length >= DIAGNOSTICS_MAX_HTTP_REQUEST_RESPONSE_IN_QUEUE) {
        bizagi.services.ajax.mobileHttpQueue.shift();
      }
      bizagi.services.ajax.mobileHttpQueue.push(ajaxMobile);

      // validate if is an error in general response
      if (ajaxMobile && ajaxMobile.general && ajaxMobile.general.statusCode != DIAGNOSTICS_GREAT_STATUS_CODE) {
        // already bizagiWorkportal.dataservice
        if (typeof bizagiWorkportal !== "undefined" && typeof bizagiWorkportal.deviceFactory !== "undefined" &&
        typeof bizagiWorkportal.deviceFactory.workportal !== "undefined" && typeof bizagiWorkportal.deviceFactory.workportal.dataService !== "undefined") {
          bizagiWorkportal.deviceFactory.workportal.dataService.addDiagnosticsError(ajaxMobile, 'HTTP_NETWORK_ERROR', 'bizagi.services.ajax.js', 688);
        } else
        {
          // for local trace when bizagiWorkportal.dataservice don't already
          bizagi.services.ajax.mobileHttpErrorsQueue.push(ajaxMobile);
        }
      }
    }
  }
}

function configureMobileRequestTraces(jqXHR) {
  // Interceptor only for mobile
  if (typeof bizagi.isMobile !== "undefined" && bizagi.isMobile() && jqXHR) {

    // This is for response
    bizagi.services.ajax.mobileGeneral.statusCode = jqXHR.status;
    bizagi.services.ajax.mobileGeneral.statusCodeText = jqXHR.status + ' ' + jqXHR.statusText;
    bizagi.services.ajax.mobileResponse.headers = jqXHR.getAllResponseHeaders();
    bizagi.services.ajax.mobileResponse.responseText = jqXHR.responseText;

    // Save request / response
    saveMobileRequestResponse();

    // Clear information ajax / mobile
    clearInformationAjaxforMobile();
  }
}

function configureBeforeRequestMethodDefinition(jqXHR, settings) {
  if (!settings.avoidLoader && settings.dataType == "json" && !settings.batchRequest) {
    jqXHR._loading = true;
    bizagi && bizagi.loading && bizagi.loading.start();
  }

  // Interceptor only for mobile
  if (typeof bizagi.isMobile !== "undefined" && bizagi.isMobile() && settings) {
    bizagi.services.ajax.mobileGeneral.requestURL = settings.url;
    bizagi.services.ajax.mobileGeneral.requestMethod = settings.type;
  }
}