/**
 * MFA Account
 */

$.Class.extend("bizagi.login_multiple.widgets.requestcode", {}, {
  init: function init(loginFacade, dataService) {
    this.loginFacade = loginFacade;
    this.dataService = dataService;
    this.content;
  },
  renderContent: function renderContent(params) {
    var self = this;
    var def = new $.Deferred();
    var templateWrapper = self.loginFacade.getTemplate("login-wrapper");
    var wrapper = "";
    var productLogo = (BIZAGI_ENVIRONMENT === 'release' ? 'jquery/production/' + bizagi.loader.productBuild + '/desktop/css/' : 'jquery/loginexternal_multiple/css/desktop/') + 'images/ico-bizagi.png';
    // Set global parameters
    self.params = params || {};

    $.when(self.dataService.getLogo()).done(function (dataLogo) {
      var themeBuilderLogo = dataLogo.logo; //Base64 image  
      var logo = typeof themeBuilderLogo != "undefined" && themeBuilderLogo != "" ? themeBuilderLogo : productLogo;

      // Render wrapper
      wrapper = $.tmpl(templateWrapper, {
        logo: logo });


      // Set wrapper content
      self.content = wrapper;
      def.resolve(wrapper);
    }).fail(function (data) {
      //Something went wrong, just show bizagi logo

      wrapper = $.tmpl(templateWrapper, {
        logo: productLogo });


      // Set wrapper content
      self.content = wrapper;
      def.resolve(wrapper);
    });
    return def.promise();
  },
  getContent: function getContent() {
    return this.content;
  } });